.worker img{
    width: 72px;
    height: 72px;
    border-radius: 50%;
}
.worker a{
    text-decoration: none;
}
.workerWrapper{
    max-width:1280px;
    height:72px;
    margin-bottom: 12px;
    display: flex;
    position: relative;
}

.infoWorker{
    margin: 16px;
    width:1156px;
}
.name{
    display:flex;
    height: 16px;
}
.name h3{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #050510;
    margin: 0px 0px;
    align-items: center;
    align-self: flex-end;
}
.name p{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #97979B;
    margin: 0px 4px;
    align-items: center;
    align-self: flex-end;
}
.department{
    margin-top: 3px;
    height: 16px;
    width:100px;
    display: block;
}
.department p{
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    color: #55555C;
    margin: 0px;
    align-self: center;
    flex: none;
    order: 1;
    flex-grow: 0;
}
.bithText p{
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 20px;
    text-align: right;
    color: #55555C;
}
.bithText{
    display: block;
    height: 20px;
    margin: 0px 4px 0px auto;
}
@media screen and (min-width: 20px) and (max-width: 1200px) {
    .worker{
        margin-left: 3%;
    }
}