html {
  overflow-y: overlay;
}
.App {
  width:100%;
  max-width:1280px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  left: 0px;
  right: 0px;
  top: 0px;
}

a.active{
  color: #050510;
  border-bottom: 2px solid #6534FF;
}
@media screen and (min-width: 20px) and (max-width: 1200px) {
  a.active{
    border-bottom: none;
  }
  a.active span{
    color: #050510;
    border-bottom: 2px solid #6534FF;
    padding-bottom: 5px;
  }
}